<template>
  <div class="tableImage d-flex align-center justify-center">
    <div
      v-if="matchDatabase"
      class="myTransparent pa-6 col-11 col-sm-8"
      style="border-radius: 20px"
    >
      <div class="d-flex d-flex justify-space-between align-end white--text">
        <h1 class="text-h6 text-sm-h4 font-weight-bold">
          {{ this.$store.state.cData.company_name }}
        </h1>

        <span class="text-h6"> ver.{{ this.$store.state.verMsg }} </span>
      </div>
      <v-divider color="white"></v-divider>

      <v-form
        class="mt-3 pa-3 setBgColor-1"
        ref="form"
        style="border-radius: 0px 0px 10px 10px"
      >
        <v-text-field
          label="帳號 (Account/E-mail)"
          prepend-icon="mdi-codepen"
          autofocus
          v-model="list.account"
          :rules="emptyRules"
          @keyup.enter="checkLogin"
        ></v-text-field>
        <v-text-field
          type="password"
          label="密碼 (Password)"
          prepend-icon="mdi-onepassword"
          v-model="list.password"
          :rules="emptyRules"
          @keyup.enter="checkLogin"
        ></v-text-field>

        <v-btn
          block
          :color="$store.state.colorSet.bgColor4"
          class="mt-2 pa-2 white--text font-weight-bold"
          @click.stop="checkLogin"
          >登入</v-btn
        >

        <div class="d-flex justify-end mt-2">
          <span>{{ this.$store.state.cData.last_login_time }}</span>
          <v-spacer></v-spacer>
          <v-icon>mdi-registered-trademark</v-icon>
          <span>2021.2.24</span>
        </div>
      </v-form>
    </div>

    <div v-else class="text-center setBgColor-4 white--text pa-5 rounded">
      <h2>伺服器未登入...</h2>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      matchDatabase: false,
      list: {},
      emptyRules: [(v) => !!v || "不可空白"],
      diylogin: false,
    };
  },

  created() {
    this.getCompData();
  },

  mounted() {
    this.loginedCheck();
  },

  methods: {
    //判斷是否已登入
    loginedCheck() {
      if (sessionStorage.getItem("logined") == "logined") {
        this.$router.push("/main/functionlist");
      }
    },
    //取得廠商資料
    getCompData() {
      this.$api
        .options("login/getCData/" + this.$store.state.databaseName)
        .then((rs) => {
          if (rs.cData) {
            this.$store.commit("setcData", rs.cData);
            sessionStorage.setItem("cData", JSON.stringify(rs.cData));
            this.matchDatabase = true;
          } else {
            this.matchDatabase = false;
          }
        });
    },
    //登入判斷
    checkLogin() {
      if (this.$refs.form.validate()) {
        this.$api
          .options(
            "login/logining/" + this.$store.state.databaseName,
            this.list
          )
          .then((rs) => {
            if (rs == "databaseError") {
              this.$swal({
                icon: "error",
                text: "資料庫未正常連結!! 請按F5後重新登入~",
              });
            } else if (rs.state == "logined") {
              if (rs.pData.suspension) {
                this.$swal({ icon: "error", text: "停權中!!! 無法登入!!" });
              } else {
                this.$store.commit("setpData", rs.pData);
                sessionStorage.setItem("pData", JSON.stringify(rs.pData));
                sessionStorage.setItem("logined", "logined");
                localStorage.setItem(
                  "lastbranch",
                  JSON.stringify(this.selectedBranch)
                );
                // this.$store.commit("logined", true);
                this.$router.push("/main/functionlist");
                this.$swal({ icon: "success", text: "登入成功" });
              }
            } else {
              this.$swal({ icon: "error", text: "未授權的登入者" });
            }
          });
      }
    },
  },
};
</script>

<style lang="scss">
body::-webkit-scrollbar {
  display: none;
}

.login {
  height: 100vh;
  background-color: blue;
  background-image: url("../../assets/BG_top.png");
  background-repeat: repeat;
}
</style>
